<template>
  <div class="checkout-success">
    <div class="success-card">
      <span class="material-symbols-rounded success-icon">check_circle</span>
      <h1>Payment Successful!</h1>
      <p>Thank you for your purchase. Your order has been confirmed.</p>
      <div class="order-info" v-if="orderDetails">
        <h2>Order Details</h2>
        <div class="order-summary">
          <p><strong>Order ID:</strong> {{ orderDetails.id }}</p>
          <p><strong>Amount Paid:</strong> ${{ (orderDetails.amount_total / 100).toFixed(2) }}</p>
          <p><strong>Email:</strong> {{ orderDetails.customer_email }}</p>
        </div>
      </div>
      <div class="success-actions">
        <router-link to="/store" class="continue-shopping">
          Continue Shopping
        </router-link>
        <router-link to="/profile/orders" class="view-order">
          View Orders
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { supabase } from '@/services/supabase'
import cart from '@/store/cart'

export default {
  name: 'CheckoutSuccess',
  setup() {
    const route = useRoute()
    const orderDetails = ref(null)

    onMounted(async () => {
      const sessionId = route.query.session_id
      if (sessionId) {
        try {
          const { data, error } = await supabase
            .functions.invoke('verify-payment', {
              body: { sessionId }
            })

          if (error) throw error
          orderDetails.value = data

          // Clear cart after successful payment
          cart.clear()
        } catch (error) {
          console.error('Error verifying payment:', error)
        }
      }
    })

    return {
      orderDetails
    }
  }
}
</script>

<style scoped>
.checkout-success {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
}

.success-card {
  background: var(--card-background);
  border-radius: 24px;
  padding: 48px;
  text-align: center;
  max-width: 480px;
  width: 100%;
  border: 1px solid var(--card-border);
}

.success-icon {
  font-size: 64px;
  color: var(--system-green);
  margin-bottom: 24px;
}

.success-card h1 {
  margin: 0 0 16px;
  color: var(--text-primary);
}

.success-card p {
  color: var(--text-secondary);
  margin: 0 0 32px;
}

.order-info {
  text-align: left;
  background: var(--surface-background);
  border-radius: 16px;
  padding: 24px;
  margin-bottom: 32px;
}

.order-info h2 {
  margin: 0 0 16px;
  font-size: 18px;
}

.order-summary p {
  margin: 8px 0;
}

.success-actions {
  display: flex;
  gap: 16px;
}

.success-actions a {
  flex: 1;
  padding: 12px 24px;
  border-radius: 12px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.2s ease;
}

.continue-shopping {
  background: var(--accent-primary);
  color: white;
}

.view-order {
  background: var(--surface-background);
  color: var(--text-primary);
}

.success-actions a:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}
</style>
